import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 4px;
  border: 2px solid gray;
  font-size: 16pt;
  background-color: #ffffff;
  padding: 10px;
  font-weight: 900;
  color: var(--secondary-text);
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: var(--primary);
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--primary-text);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 300px;
  @media (min-width: 767px) {
    width: 867px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledImg = styled.img`
  border: 4px var(--secondary);
  background-color: white;
  border-radius: 4px;
  padding: 4px;
  filter: drop-shadow(0px 0px 40px #4444aa);
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click buy to mint your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(blockchain.account, mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `Congratulations on summoning your Fantohmie! Go check it out by clicking the Paintswap link below!`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 10) {
      newMintAmount = 10;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 24, backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.jpg" : null}
      >
        <StyledLogo alt={"logo"} src={"/config/images/logo.png"} />
          <s.Container
              flex={2}
              jc={"center"}
              ai={"center"}
              style={{
                  width: "50%",
                  backgroundColor: "rgba(0,0,0,0.7)",
                  fontWeight: "bold",
                  fontSize: 22,
                  color: "#FFFFFF",
                  textShadow: "1px 1px 2px #333333",
                  padding: 12,
                  borderRadius: 4,
                  border: "4px var(--secondary)",
                  boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
              }}
          >
              A Fantom based NFT for your ghoulish delight
          </s.Container>
        <s.SpacerSmall />
        <ResponsiveWrapper flex={1} style={{ padding: 0 }} test>
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg alt={"Fantohmies #52 Bleeding Light Wraith"} src={"/config/images/example.jpg"} />
            <s.Container
                flex={2}
                jc={"center"}
                ai={"center"}
                style={{
                  backgroundColor: "#333333",
                  fontWeight: "bold",
                  fontSize: 18,
                  color: "#ffffff",
                  fontColor: "#ffffff",
                  textShadow: "1px 1px 2px #000000",
                  padding: 12,
                  borderRadius: 4,
                  boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
                }}
            >#52 Bleeding Light Wraith
            </s.Container>
          </s.Container>
          <s.SpacerLarge />
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              textShadow: "2px 2px 0px #000000",
              backgroundColor: "rgba(80, 80, 90, .9)",
              padding: 24,
              borderRadius: 4,
              border: "4px var(--secondary)",
              boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
            }}
          >
              <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)", textShadow: "0px 0px 10px lightgreen" }}
              >
                  Mint for only {CONFIG.DISPLAY_COST} {CONFIG.NETWORK.SYMBOL} while supplies last.
              </s.TextTitle>
            <s.TextTitle
              style={{
                textAlign: "center",
                fontSize: 50,
                fontWeight: "bold",
                color: "var(--accent-text)",
              }}
            >
              {data.totalSupply} / {CONFIG.MAX_SUPPLY}
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
                fontFamily: "sans-serif"
              }}
            >
              <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK} style={{color: "#ccc"}}>
                {truncate(CONFIG.CONTRACT_ADDRESS, 15)}
              </StyledLink>
            </s.TextDescription>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Connect to the {CONFIG.NETWORK.NAME} network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "BUSY" : "MINT NOW"}
                      </StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
              <ResponsiveWrapper flex={1} style={{ padding: 0 }} test>

              <StyledButton flex={1}
                            style={{ textAlign: "center", backgroundColor: "#FFFFFF" }}
                  onClick={(e) => {
                      e.preventDefault();
                      window.open(CONFIG.MARKETPLACE_LINK);
                  }}
              >
                  View Collection on PaintSwap             <StyledImg
                  alt={"PaintSwap icon"}
                  src={"/config/images/paintswap.png"}
                  style={{ width:32, verticalAlign: "text-bottom"}}
              />
              </StyledButton>
              <s.SpacerSmall></s.SpacerSmall>
              <StyledButton flex={2}
                            style={{ textAlign: "center", backgroundColor: "#FFFFFF" }}
                  onClick={(e) => {
                      e.preventDefault();
                      window.open(CONFIG.MARKETPLACE_LINK);
                  }}
              >
                  Follow on Twitter             <StyledImg
                  alt={"Twitter icon"}
                  src={"/config/images/twitter.png"}
                  style={{ width:32, verticalAlign: "text-bottom"}}
              />
              </StyledButton>
              </ResponsiveWrapper>
          </s.Container>
          <s.SpacerLarge />
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg
              alt={"Fantohmies #196 Rotting Flame Demon"}
              src={"/config/images/example2.jpg"}
              style={{ transform: "scaleX(-1)" }}
            />
              <s.Container
                  flex={2}
                  jc={"center"}
                  ai={"center"}
                  style={{
                      backgroundColor: "#333333",
                      fontWeight: "bold",
                      fontSize: 18,
                      color: "#ffffff",
                      fontColor: "#ffffff",
                      textShadow: "1px 1px 2px #000000",
                      padding: 12,
                      borderRadius: 4,
                      boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
                  }}
              >
                  #196 Rotting Flame Demon
            </s.Container>
          </s.Container>
        </ResponsiveWrapper>
        <s.SpacerMedium />
          <s.SpacerLarge></s.SpacerLarge>
          <ResponsiveWrapper flex={1} style={{ padding: 0 }} test>
              <s.SpacerLarge></s.SpacerLarge>
              <s.Container flex={1} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <StyledImg alt={"example"} src={"/config/images/descrip1.png"} style={{backgroundColor: "#333333", padding: "20px"}} />
                  </s.Container>
              </s.Container>
              <s.Container flex={2} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <s.TextDescription>Each Fantohmie is one-of-a-kind ethereal being that has been summoned with specific traits that make it unique. Collect from Wraiths, Phantoms, Specters, and more...</s.TextDescription>
                  </s.Container>
              </s.Container>
          </ResponsiveWrapper>
          <ResponsiveWrapper flex={1} style={{ padding: 0 }} test>
              <s.SpacerLarge></s.SpacerLarge>
              <s.Container flex={2} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <s.TextDescription>Fantohmies are ready to haunt the metaverse using their special powers. Find beings with powers like Electronic, Light, and Diamond! </s.TextDescription>
                  </s.Container>
              </s.Container>
              <s.SpacerLarge></s.SpacerLarge>
              <s.Container flex={1} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <StyledImg alt={"example"} src={"/config/images/descrip2.png"} style={{backgroundColor: "#333333", padding:"20px"}}/>
                  </s.Container>
              </s.Container>
              <s.SpacerLarge></s.SpacerLarge>
          </ResponsiveWrapper>
          <ResponsiveWrapper flex={1} style={{ padding: 0 }} test>
              <s.SpacerLarge></s.SpacerLarge>
              <s.Container flex={1} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <StyledImg alt={"example"} src={"/config/images/descrip3.png"}  style={{backgroundColor: "#333333", padding:"20px"}}/>
                  </s.Container>
              </s.Container>
              <s.Container flex={2} jc={"center"} ai={"center"}>
                  <s.Container flex={1} jc={"center"} ai={"center"}>
                      <s.TextDescription>Fantohmie's are also recognized by their aura. Find that special ghoul by combining Auras like Rotting, Bleeding, and Tarnished. </s.TextDescription>
                  </s.Container>
              </s.Container>
          </ResponsiveWrapper>
          <s.SpacerLarge></s.SpacerLarge>
        <s.Container jc={"center"} ai={"center"} style={{ width: "60%"}}>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
              fontSize: "12pt"
            }}
          >
            Please make sure you are connected to the right network (
            {CONFIG.NETWORK.NAME} Mainnet) and the correct address. Please note:
            Once you make the purchase, you cannot undo this action.
          </s.TextDescription>
          <s.SpacerSmall />
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)", fontSize: "12pt"
            }}
          >
            We have set the gas limit to {CONFIG.GAS_LIMIT} for the contract to
            successfully mint your NFT. We recommend that you don't lower the
            gas limit.
          </s.TextDescription>
            <s.SpacerLarge></s.SpacerLarge>
        <s.TextDescription
            style={{
                textAlign: "center",
                color: "var(--primary-text)",
                fontSize: "12pt"

            }}
        >
            <StyledButton
                style={{ textAlign: "center", backgroundColor: "#FFFFFF" }}
                onClick={(e) => {
                    e.preventDefault();
                    window.open(CONFIG.MARKETPLACE_LINK);
                }}
            >
                View Fantohmies on PaintSwap             <StyledImg
                alt={"PaintSwap icon"}
                src={"/config/images/paintswap.png"}
                style={{ width:32, verticalAlign: "text-bottom"}}
            />
            </StyledButton>
            <s.SpacerSmall></s.SpacerSmall>
            Follow <StyledLink target={"_blank"} href="https://twitter.com/fantohmies">@fantohmies</StyledLink> on Twitter for updates and giveaways.
            <s.SpacerSmall></s.SpacerSmall>
            An NFT experiment by <StyledLink target={"_blank"} href="https://twitter.com/KINCH256">KINCH</StyledLink>.
            Big thanks to <StyledLink target={"_blank"} href="https://hashlips.online/HashLips">Hashlips.</StyledLink>
        </s.TextDescription>
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
